.cart {
    padding-top: 120px;
    padding-bottom: 50px;
    min-height: 100vh;
    &__img {
        display: block;
        width: 300px;
        margin: 45px auto 60px;
    }
    &__descr {
        margin-top: 10px;
        color: rgba(255, 255, 255, 0.801);
        text-align: center;
        font-weight: 300;
    }
    &__btn {
        width: 100px;
        height: 45px;
        display: inline-block;
        font-weight: 400;
        line-height: 1.5;
        text-align: center;
        text-decoration: none;
        vertical-align: middle;
        cursor: pointer;
        user-select: none;
        background-color: transparent;
        border: 1px solid transparent;
        padding: 0.375rem 0.75rem;
        font-size: 1rem;
        border-radius: 0.25rem;
        color: #fff;
        background-color: rgba(0, 0, 0, 0.7);
        transition: .4s;
        margin-top: 15px;
        position: relative;
        left: 100%;
        transform: translateX(-100%);
        &:hover {
            transition: .4s all;
            background: rgb(108, 122, 63);
            border-color: rgb(108, 122, 63);
        }
        &-back {
            display: block;
            width: 150px;
            height: 50px;
            font-weight: 500;
            border: none;
            background-color: rgb(255, 255, 255);
            border-radius: 4px;
            color: #000;
            font-size: 16px;
            cursor: pointer;
            margin: 20px auto;
            text-align: center;
            padding-top: 15px;
            text-decoration: none;
            transition: .3s all ease-out;
            &:hover {
                color: #fff;
                background-color: #000;
            }
        }
    }

    &__title {
        font-size: 36px;
        color: #fff;
        text-align: center;
        font-weight: 300;
    }

    &__list {
        width: 60%;
        margin: 0 auto;
    }

    &__item {
        position: relative;
        display: flex;
        justify-content: space-between;
        align-items: center;
        margin-top: 40px;
        border: 1px solid rgba(256, 256, 256, .2);
        background-color: rgba(0, 0, 0, .7);
        box-shadow: 0 0 20px rgba(256, 256, 256, .1);

        &-counter {
            color: #fff;
            font-size: 24px;
        }

        &-title,
        &-price {
            width: 250px;
            text-align: center;
            color: #fff;
            font-size: 24px;
        }

        &-img {
            height: 125px;
            width: 250px;
            object-fit: cover;
        }
    }

    &__close {
        position: absolute;
        width: 25px;
        height: 25px;
        text-align: center;
        top: -10px;
        right: -10px;
        color: #fff;
        font-size: 20px;
        border-radius: 100%;
        border: 1px solid #fff;
        cursor: pointer;
    }
}